/* ########## NAVBAR ######### */
/* ########################### */

.navbar-inverse {
  z-index: 1;
  background-color: white;
  border-bottom: 6px solid #8fa540;
  margin-bottom: 25px;
  width: 100%;
}

.navbar a {
  font-size: 25px;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  color: rgb(43, 59, 38);
  text-decoration: none;
  text-transform: uppercase;
}

#logout {
  border: 1px solid rgb(201, 198, 198);
  border-radius: 10px;
  box-shadow: 0px 0px 5px 7px rgba(0, 0, 0, 0.03);
  background-color: #8fa540;
  color: white;
  padding: 3px;
}

.navbar a:hover {
  color: #015a295e;
}

.logo {
  height: 45px;
}
